<template>
  <div class>
    <v-container>
      <v-toolbar flat color="white">
        <v-text-field v-model="search" append-icon="search" label="Search" single-line hide-details></v-text-field>
        <v-spacer></v-spacer>

        <v-btn primary color="primary" class="mb-0" @click="dialog=true">追加する</v-btn>
      </v-toolbar>
      <!-- データテーブル部分
        rows-per-page-text="" 1ページあたり件数テキストの非表示
        :rows-per-page-items="[]" 1ページあたり件数セレクトの非表示
      -->
      <v-data-table
        :headers="headers"
        :items="followFromQueryList"
        :search="search"
        rows-per-page-text
        :rows-per-page-items="[]"
        class="elevation-1"
      >
        <!-- テーブルボディ -->
        <template slot="items" slot-scope="props">
          <td class="text-xs-center">{{ props.item.query }}</td>
          <td class="text-xs-center">{{ props.item.maximum_follow_number }}</td>
          <td class="justify-center layout px-0">
            <!-- 編集ボタン -->
            <v-icon class="mr-2" @click="editFollowFromQuery(props.item.id)">edit</v-icon>
            <!-- 削除ボタン -->
            <v-icon @click="openDeleteDialog(props.item.id)">delete</v-icon>
          </td>
        </template>
        <!-- 検索結果なし -->
        <v-alert slot="no-results" :value="true" color="error" icon="warning">"{{ search }}" の検索結果なし</v-alert>
        <!-- データなしの時の表示 -->
        <template slot="no-data">No Data</template>

        <!-- フッターの件数表示 -->
        <template
          slot="pageText"
          slot-scope="props"
        >{{ props.itemsLength }} 件中 {{ props.pageStart }} 件目 〜 {{ props.pageStop }} 件目</template>
      </v-data-table>
    </v-container>
    <!-- 入力ダイアログのレイアウト -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" lazy persistent max-width="600px">
        <v-form v-model="valid" lazy-validation>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex>
                    <v-text-field
                      v-model="selectedFollowFromQuery.query"
                      label="検索クエリ"
                      :rules="queryRules"
                      required
                    ></v-text-field>
                  </v-flex>
                  <v-flex>
                    <v-text-field
                      v-model="selectedFollowFromQuery.maximum_follow_number"
                      label="フォロー数"
                      :rules="maximumFollowNumberRules"
                    ></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" flat @click="close">キャンセル</v-btn>
              <v-btn color flat @click="save" :disabled="!valid">保存する</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
      </v-dialog>
    </v-layout>

    <!-- 削除確認ダイアログのレイアウト -->
    <v-layout row justify-center>
      <v-dialog v-model="deleteDialog" lazy persistent max-width="290px">
        <v-card>
          <v-card-text>削除しますか？</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" flat @click="deleteFollowFromQuery">削除</v-btn>
            <v-btn color flat @click="close">キャンセル</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>

<script>
import axios from "axios";
import router from "../router.js";
axios.defaults.headers.common["Authorization"] =
  "JWT " + localStorage.getItem("access_token");
axios.interceptors.response.use(
  function(response) {
    return response;
  },
  function(error) {
    if (error.response.status === 403) {
      router.push({ name: "login" });
    } else if (error.response.status === 400) {
      console.log("Error : " + response);
    }
    return error;
  }
);

export default {
  name: "list",
  data() {
    return {
      // ユーザ一覧が入る配列
      followFromQueryList: [],
      // テーブルヘッダ
      headers: [
        { text: "検索クエリ", align: "center", value: "query" },
        { text: "フォロー数", align: "center", value: "maximum_follow_count" },
        { text: "操作", align: "center", value: "action", sortable: false }
      ],
      // 選択中のレコードの値
      selectedFollowFromQuery: {
        id: -1,
        query: "",
        maximum_follow_count: ""
      },
      // レコードの初期値
      defaultValue: {
        id: -1,
        query: "",
        maximum_follow_count: ""
      },
      search: "", // フィルタリング検索キーワード
      dialog: false, // 新規＆更新入力フォーム
      deleteDialog: false, // 削除確認ダイアログ
      valid: false, // バリデーション結果
      queryRules: [
        v => !!v || "検索クエリは必須です",
        v => v.length <= 128 || "検索クエリは128文字までです"
      ],
      maximumFollowNumberRules: [
        v => /^[0-9]*$/.test(v) || "フォロー数は数値で入力してください"
      ]
    };
  },
  methods: {
    retrieveFollowFromQuery() {
      return new Promise((resolve, reject) => {
        axios
          .get(
            "/dashboard/followfromquery/?application=" +
              this.$store.getters.selectedApplicationId
          )
          .then(response => {
            this.followFromQueryList = response.data;
          });
      });
    },
    createFollowFromQuery(followFromQueryInfo) {
      return new Promise((resolve, reject) => {
        axios
          .post("/dashboard/followfromquery/", {
            application: this.$store.getters.selectedApplicationId,
            query: followFromQueryInfo.query,
            maximum_follow_number: followFromQueryInfo.maximum_follow_number
          })
          .then(response => {
            this.retrieveFollowFromQuery();
            this.close();
          });
      });
    },
    // ユーザ編集(書き込み)
    updateFollowFromQuery(followFromQueryInfo) {
      return new Promise((resolve, reject) => {
        axios
          .put("/dashboard/followfromquery/" + followFromQueryInfo.id + "/", {
            application: this.$store.getters.selectedApplicationId,
            query: followFromQueryInfo.query,
            maximum_follow_number: followFromQueryInfo.maximum_follow_number
          })
          .then(response => {
            this.retrieveFollowFromQuery();
            this.close();
          });
      });
    },
    // ユーザ編集(読み出し)
    editFollowFromQuery(id) {
      return new Promise((resolve, reject) => {
        axios.get("/dashboard/followfromquery/" + id + "/").then(response => {
          this.selectedFollowFromQuery = response.data;
          this.dialog = true;
        });
      });
    },
    // 削除ダイアログ
    openDeleteDialog(id) {
      this.selectedFollowFromQuery.id = id;
      this.deleteDialog = true;
    },
    // ユーザ削除
    deleteFollowFromQuery() {
      return new Promise((resolve, reject) => {
        axios
          .delete(
            "/dashboard/followfromquery/" +
              this.selectedFollowFromQuery.id +
              "/"
          )
          .then(response => {
            this.retrieveFollowFromQuery();
            this.close();
          });
      });
    },
    // ダイアログの保存ボタン
    save() {
      if (this.selectedFollowFromQuery.id == -1) {
        // 作成
        this.createFollowFromQuery(this.selectedFollowFromQuery);
      } else {
        // 更新
        this.updateFollowFromQuery(this.selectedFollowFromQuery);
      }
    },
    // ダイアログのキャンセルボタン
    close() {
      this.dialog = false;
      this.deleteDialog = false;
      setTimeout(() => {
        this.selectedFollowFromQuery = Object.assign({}, this.defaultValue);
      }, 500);
    }
  },
  computed: {
    // ダイアログのタイトルを作成と更新で使い分ける
    formTitle() {
      return this.selectedFollowFromQuery.id === -1 ? "新規追加" : "編集";
    },
    selectApplication: function() {
      return this.$store.state.selectedApplicationId;
    }
  },
  watch: {
    selectApplication() {
      this.$nextTick(() => {
        this.retrieveFollowFromQuery();
      });
    }
  },
  mounted() {
    this.retrieveFollowFromQuery();
  }
};
</script>