import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);
axios.defaults.baseURL = "http://127.0.0.1:8001/api/v1";
axios.defaults.baseURL = "http://52.68.229.41/api/v1";
axios.defaults.headers.common["Authorization"] =
  "Bearer " + localStorage.getItem("access_token");

export default new Vuex.Store({
  state: {
    token: localStorage.getItem("access_token") || null,
    selectedApplicationId:
      localStorage.getItem("selected_application_id") || null
  },
  getters: {
    loggedIn(state) {
      return state.token !== null;
    },
    selectedApplicationId(state) {
      return state.selectedApplicationId;
    }
  },
  mutations: {
    retrieveToken(state, token) {
      state.token = token;
    },
    destroyToken(state) {
      state.token = null;
    },
    selectApplication(state, selectedApplicationId) {
      state.selectedApplicationId = selectedApplicationId;
    }
  },
  actions: {
    register(context, data) {
      return new Promise((resolve, reject) => {
        axios
          .post("/rest-auth/registration/", {
            email: data.email,
            password1: data.password1,
            password2: data.password2
          })
          .then(response => {
            const token = response.data.token;
            localStorage.setItem("access_token", token);
            context.commit("retrieveToken", token);
            resolve(response);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    destroyToken(context) {
      if (context.getters.loggedIn) {
        return new Promise((resolve, reject) => {
          axios
            .post("/logout")
            .then(response => {
              localStorage.removeItem("access_token");
              context.commit("destroyToken");
              resolve(response);
            })
            .catch(error => {
              localStorage.removeItem("access_token");
              context.commit("destroyToken");
              reject(error);
            });
        });
      }
    },
    retrieveToken(context, credentials) {
      return new Promise((resolve, reject) => {
        axios
          .post("/rest-auth/login/", {
            email: credentials.email,
            password: credentials.password
          })
          .then(response => {
            const token = response.data.token;
            localStorage.setItem("access_token", token);
            context.commit("retrieveToken", token);
            resolve(response);
            // context.commit('addTodo', response.data)
          })
          .catch(error => {
            console.log(error);
            reject(error);
          });
      });
    },
    selectApplication(context, selectedApplicationId) {
      context.commit("selectApplication", selectedApplicationId);
      localStorage.setItem("selected_application_id", selectedApplicationId);
    }
  }
});
