<template>
  <v-container>
    <v-card>
      <v-container>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="name"
            :rules="nameRules"
            label="Application Name"
            required
            :error-messages="errorName"
          ></v-text-field>

          <v-text-field
            v-model="consumerKey"
            :rules="nameRules"
            label="Consumer Key"
            required
            :error-messages="errorConsumerKey"
          ></v-text-field>

          <v-text-field
            v-model="consumerSecret"
            :rules="nameRules"
            label="Consumer Secret"
            required
            :error-messages="errorConsumerSecret"
          ></v-text-field>

          <v-text-field
            v-model="accessToken"
            :rules="nameRules"
            label="Access Token"
            required
            :error-messages="errorAccessToken"
          ></v-text-field>

          <v-text-field
            v-model="accessTokenSecret"
            :rules="nameRules"
            label="Access Token Secret"
            required
            :error-messages="errorAccessTokenSecret"
          ></v-text-field>

          <v-btn router :to="{name: 'applicationList'}">キャンセル</v-btn>
          <v-btn :disabled="!valid" color="primary" @click="submit">保存する</v-btn>
        </v-form>
      </v-container>
    </v-card>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "ApplicationAdd",
  data() {
    return {
      valid: false,
      id: [],
      name: "",
      consumerKey: "",
      consumerSecret: "",
      accessToken: "",
      accessTokenSecret: "",
      nameRules: [v => !!v || "This field is required"],
      errorName: "",
      errorConsumerKey: "",
      errorConsumerSecret: "",
      errorAccessToken: "",
      errorAccessTokenSecret: ""
    };
  },
  methods: {
    submit() {
      return new Promise((resolve, reject) => {
        axios.defaults.headers.common["Authorization"] =
          "JWT " + localStorage.getItem("access_token");
        axios
          .post("/dashboard/applications/", {
            name: this.name,
            consumer_key: this.consumerKey,
            consumer_secret: this.consumerSecret,
            access_token: this.accessToken,
            access_token_secret: this.accessTokenSecret
          })
          .then(response => {
            console.log(response.data);
            this.$router.push({ name: "applicationList" });
          })
          .catch(error => {
            if (error.response.status === 403) {
              this.$router.push({ name: "login" });
            } else if (error.response.status === 400) {
              this.errorName = error.response.data.name;
              this.errorConsumerKey = error.response.data.consumer_key;
              this.errorConsumerSecret = error.response.data.consumer_secret;
              this.errorAccessToken = error.response.data.access_token;
              this.errorAccessTokenSecret =
                error.response.data.access_token_secret;
            }
          });
      });
    }
  }
  // created: function() {
  //     this.retrieveApplication();
  // }
};
</script>
