<template>
  <div class="applications">
    <v-container>
      <v-expansion-panel>
        <v-expansion-panel-content v-for="application in applications" :key="application.id">
          <div slot="header" class="py-1">{{ application.name }}</div>
          <v-card>
            <!-- <v-card-text class="px-4 grey--text">
              <div class="font-weight-bold">Due by {{ application.due }}</div>
              <div>{{ application.content }}</div>
            </v-card-text>-->
            <v-list two-line>
              <v-list-tile>
                <v-list-tile-content class="px-2">
                  <v-list-tile-sub-title>Consumer Key</v-list-tile-sub-title>
                  <v-list-tile-title>{{ application.consumer_key }}</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content class="px-2">
                  <v-list-tile-sub-title>Consumer Secret</v-list-tile-sub-title>
                  <v-list-tile-title>{{ application.consumer_secret }}</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content class="px-2">
                  <v-list-tile-sub-title>Access Token</v-list-tile-sub-title>
                  <v-list-tile-title>{{ application.access_token }}</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content class="px-2">
                  <v-list-tile-sub-title>Access Token Secret</v-list-tile-sub-title>
                  <v-list-tile-title>{{ application.access_token_secret }}</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-list>
            <v-btn
              color
              class="ml-3 mb-3"
              router
              :to="{name: 'applicationDetail', params:{ id: application.id }}"
            >編集する</v-btn>
          </v-card>
        </v-expansion-panel-content>
      </v-expansion-panel>
      <v-btn color="primary" class="mt-3" router :to="{name: 'applicationAdd'}">追加する</v-btn>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Application",
  data() {
    return {
      applications: [],
      id: [],
      name: "",
      consumerKey: "",
      consumerSecret: "",
      accessToken: "",
      accessTokenSecret: ""
    };
  },
  methods: {
    retrieveApplication() {
      return new Promise((resolve, reject) => {
        axios.defaults.headers.common["Authorization"] =
          "JWT " + localStorage.getItem("access_token");
        axios
          .get("/dashboard/applications/")
          .then(response => {
            this.applications = response.data;
          })
          .catch(error => {
            if (error.response.status === 403) {
              this.$store.dispatch("destroyToken");
              this.$router.push({ name: "login" });
            }
          });
      });
    }
  },
  created: function() {
    this.retrieveApplication();
  }
};
</script>
