<template>
  <v-container>
    <v-card>
      <v-container>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field v-model="name" :rules="nameRules" label="Application Name" required></v-text-field>

          <v-text-field v-model="consumerKey" :rules="nameRules" label="Consumer Key" required></v-text-field>

          <v-text-field v-model="consumerSecret" :rules="nameRules" label="Consumer Secret" required></v-text-field>

          <v-text-field v-model="accessToken" :rules="nameRules" label="Access Token" required></v-text-field>

          <v-text-field
            v-model="accessTokenSecret"
            :rules="nameRules"
            label="Access Token Secret"
            required
          ></v-text-field>

          <v-btn router :to="{name: 'applicationList'}">キャンセル</v-btn>
          <v-btn :disabled="!valid" color="primary" @click="updateApplication">保存する</v-btn>
        </v-form>
      </v-container>
    </v-card>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "ApplicationDetail",
  props: ["id"],
  data() {
    return {
      valid: false,
      // id: [],
      name: "",
      consumerKey: "",
      consumerSecret: "",
      accessToken: "",
      accessTokenSecret: "",
      nameRules: [v => !!v || "This field is required"]
    };
  },
  methods: {
    retrieveApplication() {
      return new Promise((resolve, reject) => {
        axios.defaults.headers.common["Authorization"] =
          "JWT " + localStorage.getItem("access_token");
        axios
          .get("/dashboard/applications/" + this.id + "/")
          .then(response => {
            this.Applications = response.data;
            this.name = response.data.name;
            this.consumerKey = response.data.consumer_key;
            this.consumerSecret = response.data.consumer_secret;
            this.accessToken = response.data.access_token;
            this.accessTokenSecret = response.data.access_token_secret;
          })
          .catch(error => {
            if (error.response.status === 403) {
              this.$router.push({ name: "login" });
            }
          });
      });
    },
    updateApplication() {
      return new Promise((resolve, reject) => {
        axios.defaults.headers.common["Authorization"] =
          "JWT " + localStorage.getItem("access_token");
        axios
          .put("/dashboard/applications/" + this.id + "/", {
            name: this.name,
            consumer_key: this.consumerKey,
            consumer_secret: this.consumerSecret,
            access_token: this.accessToken,
            access_token_secret: this.accessTokenSecret
          })
          .then(response => {
            this.Applications = response.data;
            this.name = response.data.name;
            this.consumerKey = response.data.consumer_key;
            this.consumerSecret = response.data.consumer_secret;
            this.accessToken = response.data.access_token;
            this.accessTokenSecret = response.data.access_token_secret;
            this.$router.push({ name: "applicationList" });
          })
          .catch(error => {
            if (error.response.status === 403) {
              this.$router.push({ name: "login" });
            }
          });
      });
    }
  },
  created: function() {
    console.log(this.id);
    this.retrieveApplication();
  }
};
</script>
