<template>
  <nav>
    <v-snackbar v-model="snackbar" :timeout="4000" top color="success">
      <span>Awesome! You added a new project.</span>
      <v-btn color="white" flat @click="snackbar = false">Close</v-btn>
    </v-snackbar>

    <v-toolbar flat app>
      <v-toolbar-side-icon @click="drawer = !drawer" class="grey--text"></v-toolbar-side-icon>
      <v-toolbar-title class="text-uppercase grey--text">
        <!-- <span class="font-weight-light">Todo</span>
        <span>Ninja</span>-->
      </v-toolbar-title>
      <v-spacer></v-spacer>

      <!-- dropdown menu -->
      <v-menu offset-y>
        <v-btn flat slot="activator" color="grey">
          <v-icon left>expand_more</v-icon>
          <span>Menu</span>
        </v-btn>
        <v-list>
          <v-list-tile v-for="link in links" :key="link.text" router :to="link.route">
            <v-list-tile-title>{{ link.text }}</v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>

      <v-btn v-if="!loggedIn" flat color="grey" router :to="{ name: 'login' }">
        <span>Log In</span>
        <v-icon right>exit_to_app</v-icon>
      </v-btn>
      <v-btn v-if="loggedIn" flat color="grey" router :to="{ name: 'logout' }">
        <span>Sign Out</span>
        <v-icon right>exit_to_app</v-icon>
      </v-btn>
    </v-toolbar>

    <v-navigation-drawer v-if="loggedIn" app v-model="drawer" class="primary">
      <v-container fluid grid-list-xl>
        <v-layout wrap align-center>
          <v-flex d-flex>
            <v-select
              :items="applications"
              item-text="name"
              v-model="selectedApplication"
              label="アプリケーション選択"
              solo
              @change="selectApplication($event)"
            ></v-select>
          </v-flex>
        </v-layout>
      </v-container>

      <v-list>
        <v-list-tile v-for="link in links" :key="link.text" router :to="link.route">
          <v-list-tile-action>
            <v-icon class="white--text">{{ link.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title class="white--text">{{ link.text }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
  </nav>
</template>

<script>
import axios from "axios";
import Popup from "./Popup";

export default {
  components: { Popup },
  data() {
    return {
      drawer: true,
      links: [
        {
          icon: "format_list_bulleted",
          text: "アプリケーション一覧",
          route: "/applications"
        },
        {
          icon: "fiber_new",
          text: "アプリケーション追加",
          route: "/applications/add"
        },
        { icon: "settings", text: "フォロー設定", route: "/followsetting" }
      ],
      snackbar: false,
      applications: [],
      selectedApplication: null
    };
  },
  computed: {
    loggedIn() {
      return this.$store.getters.loggedIn;
    },
    loggedInFunction: function() {
      return this.$store.state.loggedIn;
    }
  },
  watch: {
    loggedInFunction() {
      this.$nextTick(() => {
        this.retrieveApplication();
      });
    }
  },
  methods: {
    retrieveApplication() {
      return new Promise((resolve, reject) => {
        axios.defaults.headers.common["Authorization"] =
          "JWT " + localStorage.getItem("access_token");
        axios
          .get("/dashboard/applications/")
          .then(response => {
            this.applications = response.data;
            this.selectedApplication = this.applications[
              Object.keys(this.applications)[0]
            ]; // セレクトボックスで最初のoptionを選択する
            this.selectApplication(
              this.applications[Object.keys(this.applications)[0]].name
            ); // 選択されたApplicationをGlobalで保持
          })
          .catch(error => {
            if (error.response.status === 403) {
              this.$store.dispatch("destroyToken");
              this.$router.push({ name: "login" });
            }
          });
      });
    },
    selectApplication(event) {
      const selectedApplicationId = this.applications.filter(
        application => application.name === event
      )[0].id;
      this.$store.dispatch("selectApplication", selectedApplicationId);
    }
  },
  created: function() {
    if (this.$store.getters.loggedIn) {
      this.retrieveApplication();
    }
  }
};
</script>

