<template>
  <v-content>
    <v-container fluid fill-height>
      <v-layout align-center justify-center>
        <v-flex xs12 sm8 md4>
          <v-card class="elevation-12">
            <v-toolbar dark color="primary">
              <v-toolbar-title>Login form</v-toolbar-title>
              <v-spacer></v-spacer>
            </v-toolbar>
            <v-card-text>
              <v-form id="registration-form" @submit.prevent="registration">
                <v-alert
                  :value="true"
                  color="error"
                  icon="warning"
                  outline
                  v-if="errorCredentials"
                >{{ errorCredentials }}</v-alert>
                <v-text-field
                  id="email"
                  prepend-icon="email"
                  name="email"
                  label="メールアドレス"
                  type="email"
                  v-model="email"
                  :error-messages="errorEmail"
                ></v-text-field>
                <v-text-field
                  id="password1"
                  prepend-icon="lock"
                  name="password1"
                  label="パスワード"
                  type="password"
                  v-model="password1"
                  :error-messages="errorPassword1"
                ></v-text-field>
                <v-text-field
                  id="password2"
                  prepend-icon="lock"
                  name="password2"
                  label="パスワード（確認用）"
                  type="password"
                  v-model="password2"
                  :error-messages="errorPassword2"
                ></v-text-field>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" type="submit" form="registration-form">会員登録</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-content>
</template>

<script>
export default {
  name: "registration",
  data() {
    return {
      email: "",
      password1: "",
      password1: "",
      errorEmail: "",
      errorPassword1: "",
      errorPassword2: "",
      errorCredentials: ""
    };
  },
  methods: {
    registration() {
      this.$store
        .dispatch("register", {
          email: this.email,
          password1: this.password1,
          password2: this.password2
        })
        .then(response => {
          this.$router.push({ name: "applicationList" });
        })
        .catch(error => {
          this.errorCredentials = "";
          this.errorEmail = error.response.data.email;
          this.errorPassword1 = error.response.data.password1;
          this.errorPassword2 = error.response.data.password2;
          this.errorCredentials = error.response.data.non_field_errors[0];
          console.log(error.response);
        });
    }
  }
};
</script>