<template>
  <v-content>
    <v-container fluid fill-height>
      <v-layout align-center justify-center>
        <v-flex xs12 sm8 md4>
          <v-card class="elevation-12">
            <v-toolbar dark color="primary">
              <v-toolbar-title>Login form</v-toolbar-title>
              <v-spacer></v-spacer>
            </v-toolbar>
            <v-card-text>
              <v-form id="login-form" @submit.prevent="login">
                <v-alert
                  :value="true"
                  color="error"
                  icon="warning"
                  outline
                  v-if="errorCredentials"
                >{{ errorCredentials }}</v-alert>
                <v-text-field
                  id="email"
                  prepend-icon="email"
                  name="email"
                  label="Login"
                  type="email"
                  v-model="email"
                  :error-messages="errorEmail"
                ></v-text-field>
                <v-text-field
                  id="password"
                  prepend-icon="lock"
                  name="password"
                  label="Password"
                  type="password"
                  v-model="password"
                  :error-messages="errorPassword"
                ></v-text-field>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" type="submit" form="login-form">ログイン</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-content>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      email: "",
      password: "",
      errorEmail: "",
      errorPassword: "",
      errorCredentials: ""
    };
  },
  methods: {
    login() {
      this.$store
        .dispatch("retrieveToken", {
          email: this.email,
          password: this.password
        })
        .then(response => {
          this.$router.push({ name: "applicationList" });
        })
        .catch(error => {
          this.errorCredentials = "";
          this.errorEmail = error.response.data.email;
          this.errorPassword = error.response.data.password;
          this.errorCredentials = error.response.data.non_field_errors[0];
          console.log(error.response);
        });
    }
  }
};
</script>